import { moment, Moment } from '../../utils/time';

export const timeframeOption = [
  {
    id: "DAY",
    label: "1D",
    calc: (t: Moment | string) => moment(t).add(-1, 'day')
  },
  {
    id: "3_DAYS",
    label: "3D",
    calc: (t: Moment | string) => moment(t).add(-3, 'day')
  },
  {
    id: "WEEK",
    label: "1W",
    calc: (t: Moment | string) => moment(t).add(-7, 'day')
  }, {
    id: "MONTH",
    label: "1M",
    calc: (t: Moment | string) => moment(t).add(-1, 'month')
  },
  {
    id: "3_MONTHS",
    label: "3M",
    calc: (t: Moment | string) => moment(t).add(-3, 'month')
  },
  {
    id: "YEAR",
    label: "1Y",
    calc: (t: Moment | string) => moment(t).add(-1, 'year')
  }
];

export default timeframeOption;