import { scaleTime, scaleLinear } from '@vx/scale';
import { TimeSeries } from '../../model/domain/metrics';
import { moment, getTimezone } from '../../utils/time'
import { ScaleTime, ScaleLinear } from 'd3-scale';

interface XScale {
  scale: ScaleTime<number, number>;
  tickFormat: (date: Date) => string;
}

interface YScale {
  scale: ScaleLinear<number, number>;
  tickFormat: (n: number) => string;
}

export function getXScale(width: number, timeSeriesResult: TimeSeries.Result): XScale {
  const { startTime, endTime } = timeSeriesResult.interval;
  
  const scale = scaleTime({
    domain: [moment(startTime), moment(endTime)],
    range: [0, width]
  });

  //timezone aware formatter
  //tricks underlying formatter by changing year, month, day, hour, minute values
  //according to timezone provided by `getTimezone`
  const tickFormat = (() => {
    const orgFormatter = scale.tickFormat();

    return (date: Date) => {
      const m = moment.tz(date.toISOString(), getTimezone());

      const d = new Date(
        m.get('year'),
        m.get('month'),
        m.get('day'),
        m.get('hour'),
        m.get('minute'));
  
      return orgFormatter(d);
    }
  })();

  return { scale, tickFormat };
}

export function getYScale(height: number, timeSeriesResult: TimeSeries.Result): YScale {
  function getDomain(tsr: TimeSeries.Result): [number, number] | null {
    if (tsr.unit === '%') {
      return [0, 1.25];
    }
    
    let min = Number.POSITIVE_INFINITY;
    let max = Number.NEGATIVE_INFINITY;
  
    tsr.timeSeries.forEach((timeSeries: TimeSeries.TimeSeries) => {
      timeSeries.points.forEach(point => {
        if (!point.isMissing) {
          if (point.value < min) min = point.value;
          if (point.value > max) max = point.value;
        }
      })
    });

    //10% empty space at the top and bottom
    const buffer = (max - min) / 10;
  
    return [min - buffer, max + buffer];
  }

  const domain = getDomain(timeSeriesResult);

  const scale = scaleLinear({
    domain,
    range: [height, 0]
  });

  //unit aware formatter
  const tickFormat = (() => {
    const orgFormatter = scale.tickFormat();

    return (n: number) => {
      if (timeSeriesResult.unit !== '%') {
        return n + '';
      }

      return Math.round(n * 100) + '%';
    }
  })();

  return { scale, tickFormat };
}