import React, { useState } from "react";
import { MenuItem, Button } from "../../style";
import moment, { Moment } from "moment";
import { primaryColor } from "../../config";
import timeframeOptions from './timeframes';
import { Popconfirm } from "antd";
import FilterBox from "./filter-box";

interface SetTime {
  (arg: { startTime: string | Moment, endTime: string | Moment }): void
}
const Filter = (props: any) => {
  const { interval: initialState, setInterval, backgroundColor, color, loading, hide } = props;
  const [active, setActive] = useState("3_DAYS");

  const [state, setState] = useState(initialState);

  const setTime: SetTime = ({ startTime, endTime }) => {
    const shouldSwap = startTime > endTime;
    
    const state = {
      startTime: moment(shouldSwap ? endTime : startTime).toISOString(),
      endTime: moment(shouldSwap ? startTime : endTime).toISOString()
    };
    setState(state);
    setInterval(state);
  };

  const submit = () => {
    setInterval(state);
    setActive('NONE');
  }

  if (hide) return null;

  return (
    <>
      <span className="hide-mobile" style={{ padding: "0 .5em" }}>{
        timeframeOptions.map(({ id, label, calc }) => (
          <MenuItem
            active={id === active}
            inverted
            key={id}
            onClick={() => {
              setActive(id)
              const endTime = moment();
              const startTime = calc(endTime);
              setTime({startTime, endTime});
            }}
          >{label}</MenuItem>))
      }
      </span>

      <Popconfirm
        placement="bottom"
        icon=""
        title={
          <FilterBox
            submit={submit}
            interval={state}
            backgroudColor={backgroundColor}
            color={color}
            setTime={setTime}
          />
        }
        onConfirm={submit}
        okText={"Generate Data"}
        cancelText=""
      >
        <a
          className="ant-dropdown-link rounded shadow"
          style={{
            color: primaryColor,
            background: "white",
            fontSize: "1.5em",
          }}
          onClick={(e) => e.preventDefault()}>
          <i className={`fa fa-${loading ? 'spinner spin' : 'filter'}`}></i>
        </a>
      </Popconfirm>
    </>
  );
};

export default Filter;
