import React from "react";
import { Text, Button } from "../../style";
import { DatePicker } from "antd";
import { moment, Moment, FORMAT_DATETIME } from '../../utils/time';
import { primaryColor } from "../../config";

const FilterBox = (props: any) => {
  const { interval, setTime, backgroundColor, color } = props;

  return (
    <div
      style={{
        display: "grid",
        height: "100%",
        backgroundColor,
        color,
        gridAutoColumns: "auto auto auto auto 1fr",
        placeItems: "end start",
      }}
    >
      <Text width="100%" textAlign="center" size={2} bold>
        Filter
      </Text>

      <Text color={primaryColor} textAlign="left" margin=".5em 0 0 0" bold>
        Start Date
      </Text>
      <DatePicker
        use12Hours
        showTime={{format: 'hh:mm a'}}
        format={FORMAT_DATETIME}
        minuteStep={15}
        style={{ borderRadius: 8 }}
        onChange={(e: Moment | null) => {
          if (!e) return;
          setTime({ startTime: e.toISOString() });
        }}
        value={moment(interval.startTime)}
      />

      <Text color={primaryColor} bold margin=".5em 0 0 0">
        End Date
      </Text>
      <DatePicker
        showTime={{format: 'hh:mm a'}}
        use12Hours
        format={FORMAT_DATETIME}
        minuteStep={15}
        style={{ borderRadius: 8 }}
        onChange={(e: Moment | null) => {
          if (!e) return;
          setTime({ endTime: e.toISOString() })
        }}
        value={moment(interval.endTime)}
      />
    </div>
  );
};


export default FilterBox;
